@import "vars";

.nav {
  margin-left: 143px;

  &__list {
    display: flex;
    justify-content: center;
    list-style: none;
  }

  &__item {
    margin-right: 63px;

    &:nth-child(5) {
      margin-right: 135px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    color: white;
    font-weight: bold;
    text-decoration: none;
    transition: color $hover_duration;

    &:hover {
      color: sienna;
    }

    &--active {
      position: relative;

      &::after {
        content: "";
        height: 4px;
        width: 120%;
        background-color: #fcc304;
        border-radius: 32px;
        position: absolute;
        bottom: - 10px;
        left: -10%;
      }
    }
  }
}
